

import React from "react";
import logo from "../images/myface.jpg";

import logo1 from "../images/myface2.jpg";

import logo2 from "../images/myface3.jpg";

import logo3 from "../images/myface4.jpg";

import SimpleImageSlider from "react-simple-image-slider";



const Profile= () =>{
    const images = [
        { url: logo },
        { url: logo1 },
        { url: logo3 },
        { url: logo2 },
    ];
    return (  
    <body>  
        <div className = "container">
            <div className='columns'>
                <div className="column">  
                    <section className="section" >
                        <div className="column logo width=100% ">
                            <SimpleImageSlider
                                width={300}
                                height={504}
                                images={images}
                            />
                        </div>
                    </section>
                </div>


                <div className='column'>
                    <div className='content'>
                        <section className="section" >
                            <h2 className="title ">Profile</h2>
                                <ul>
                                    <li>名前：金子恭平</li>
                                    <li>年齢：20歳</li>
                                    <li>出身：千葉</li>
                                    <li>所属：日本大学文理学部情報科</li>
                                </ul>
                        </section>
                        <section className="section" >
                            <h2 className="title ">Available languages</h2>
                                <ul>
                                    <li>Python</li>
                                    <li>Ruby</li>
                                    <li>C</li>
                                    <li>Javascript
                                        <ul>
                                        <li>React</li>
                                        <li>Ionic</li>
                                        </ul>
                                    </li>
                                    <li>Proseccing</li>
                                </ul>
                        </section>

                        <section className="section" >
                            <h2 className="title ">Remarks</h2>
                                <ul>
                                    <li>PAST 第三回アルゴリズム検定 58点</li>
                                    <li>Atcoder 茶色</li>
                                    <li>Ionic Japan Camp 2020 Spring @日本大学 優勝</li>
                                </ul>
                        </section>

                        <section className="section" >
                            <h2 className="title ">Jobs</h2>
                                <ul>
                                    <li>TOMAS 個別指導塾講師</li>
                                    <li>教授研究開発補助</li>
                                </ul>
                        </section>

                        
                    </div>
                </div>

                <div className="column ">
                    <div className="content">
                        <section className="section"> 
                            <h2>Links</h2>
                            <ul className='menu-list'>
                              
                                <a href='http://www.nihon-u.ac.jp/'>日本大学</a>
                                
                                <a href='https://www.chs.nihon-u.ac.jp/'>日本大学 文理学部</a>
                                
                            </ul>
                        </section>
                        
                    </div>
                </div>
            </div>
      
            
        </div>
    </body>
  );
};


export default Profile;
